<script>
import axios from "axios";
export default {
  name: "sw",
  data() {
    return {};
  },
  methods: {
    show() {
      this.myEcharts();
    },
    myEcharts(data) {
      var myChart = this.$echarts.init(document.getElementById("main-sw"));
      //配置图表
      var option = {
        xAxis: {
          type: "category",
          data: [
            "12:00",
            "13:00",
            "14:00",
            "15:00",
            "16:00",
            "17:00",
            "16:00",
            "17:00",
            "18:00",
          ],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            // data: [820, 932, 901, 934, 1290, 1330, 1320],
            data: data,
            type: "bar",
            smooth: true,
          },
        ],
      };
      myChart.setOption(option);
    },
  },
  mounted() {
    axios
      .get("https://random-data-api.com/api/app/random_app?size=9")
      .then((response) => {
        let res = response.data;
        console.log(res);
        let data = [];
        for (let i = 0; i < res.length; i++) {
          data.unshift(res[i].id);
        }
        this.myEcharts(data);
      });
  },
};
</script>

